import React, { Component } from "react";
import { Link } from "react-router-dom"
import axios from 'axios';


import classnames from "classnames/bind";

import css from "./index.scss";
import Title from "../../components/Title"

const cx = classnames.bind(css);
const moduleName = "GoodsComponent";

class GoodsComponent extends Component {
  constructor() {
    super();
    this.state = {
      data:[
        
      ],
      page:[

      ]
    };
  }
  componentDidMount () {
    axios.get('/goods')
        .then(res => {
          const data = res.data.reverse();
          this.setState({data});
          let lastPage = Math.ceil(data.length / 4)
          console.log(lastPage);
          let page = "";
          for (var i=1; i<=lastPage; i++) {
            if (i!==lastPage){
              page = page + i +",";
            }else {
              page = page + i;
            }
          }
          page = page.split(',')
          this.setState({page})
        })
    document.documentElement.scrollTop = 0;
  }
  render() {
    return (
      <div className={cx(`${moduleName}`)}>
        <Title 
          title=" "
          url1="goods"
          url2=""
          urlName1=" "
          urlName2=""/>
        {/* <div className={cx(`${moduleName}-search`)}>
          <div className={cx(`${moduleName}-search-container`)}>
            <input type="text" placeholder=" ." />
          </div>
        </div> */}
        <div className={cx(`${moduleName}-content`)}>
          <div className={cx(`${moduleName}-content-container`)}>
          </div>
        </div>
        <div className={cx(`${moduleName}-content`)}>
          {this.state.data.map((post) =>
          <Link to={`/goods/${post.id}`}>
          <div className={cx(`${moduleName}-content-card`)}>
            <div className={cx(`${moduleName}-image`)}>
              <img src={post.url} alt="image"/>
            </div>
            <div className={cx(`${moduleName}-image-title`)}>
              <h6>{post.title}</h6>
              <h6>: {post.writer}</h6>
            </div>
            <div className={cx(`${moduleName}-image-date`)}>
              <h6>: {post.date}</h6>
            </div>
          </div></Link>)}
        </div>
        <div className={cx(`${moduleName}-pagination`)}>
          <div className={cx(`${moduleName}-pagination-container`)}>
          <div className={cx(`${moduleName}-pagination-detail`)}>&lt;</div>
          {this.state.page.map((post)=><div className={cx(`${moduleName}-pagination-detail`)} value={post}>{post}</div>)}
          <div className={cx(`${moduleName}-pagination-detail`)}>&gt;</div>
          </div>
        
        </div>
      </div>
    );
  }
}

export default GoodsComponent;


