import React, { Component } from "react";
import {Link} from "react-router-dom";
import axios from 'axios';

import classnames from "classnames/bind";

import css from "./index.scss";
import Title from "../../../components/Title"
const cx = classnames.bind(css);

const moduleName = "GoodsDetailComponent";

class GoodsDetailComponent extends Component {
  constructor() {
    super();
    this.state = {
      data: [

      ],
      prevData: [

      ],
      nextData: [

      ]
    };
  }
    componentDidMount () {
        var locationArray = window.location.href.split('/')
        axios.get('/goods/'+locationArray[locationArray.length-1])
      .then(res => {
        const data = res.data;
        this.setState({data})
      }) 
      var prev =Number(locationArray[locationArray.length-1])-1 
        var next =Number(locationArray[locationArray.length-1])+1
        axios.get('/goods/'+next)
        .then(res => {
            const nextData = res.data;
            this.setState({nextData})
        })
        axios.get('/goods/'+prev)
        .then(res => {
            const prevData = res.data;
            this.setState({prevData})
        }) 
        document.documentElement.scrollTop = 0;
    }
    render() {
      return (
        <div className={cx(`${moduleName}`)}>
          <Title 
            title=""
            url1="communication"
            url2="activity"
            urlName1=" "
            urlName2=""/>
            <div className={cx(`${moduleName}-content`)}>
                <div className={cx(`${moduleName}-content-container`)}>
                    <table>
                        <tr>
                            <th width="10%">No</th>
                            <th width="70%"></th>
                            <th width="10%"></th>
                            <th width="10%"></th>
                        </tr>
                        {this.state.data.map((post) =>
                            <tr>
                                <td>{post.id}</td>
                                <td>{post.title}</td>
                                <td>{post.writer}</td>
                                <td>{post.date}</td>
                            </tr>
                        )}
                        {this.state.data.map((post) =>
                            <tr>
                                <td colspan="6"><img src={post.url} alt="image"/><br />   </td>
                            </tr>
                        )}
                        <tr>
                            <td colspan="6" className={cx(`${moduleName}-next-prev`)}>
                               : {this.state.prevData.length===0?"  .":this.state.prevData.map((post)=><a href={`/goods/${post.id}`}>{post.title}</a>)}
                            </td>
                        </tr>
                        <tr>
                            <td colspan="6" className={cx(`${moduleName}-next-prev`)}>
                             : {this.state.nextData.length===0?"  .":this.state.nextData.map((post)=><a href={`/goods/${post.id}`}>{post.title}</a>)}
                            </td>
                        </tr>
                    </table>
                </div>
                <div className={cx(`${moduleName}-content-button`)}>
                  <Link to="/goods"><button></button></Link>
                </div>
            </div>
            
        </div>
      )
    }
}
export default GoodsDetailComponent;


